<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')" class="current">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<!-- 운동 친구 소개 -->
			<p class="contents-title underline marB-20 body3">자주묻는질문 수정</p>
			<div class="input-container">
				<p class="small-title">카테고리</p>
				<div class="select-box">
					<SelectBoxAdmin
						class="small marB-20"
						:options="options"
						v-model="selected"
						placeholder="카테고리를 선택해주세요"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">제목(문의내용)</p>
					<input
						type="text"
						class="admin-input large"
						name="title"
						placeholder="타이틀"
						v-model="title"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">내용(해결방법)</p>
					<textarea
						class="textarea marB_20"
						placeholder="내용을 입력해주세요"
						v-model="contents"
					>
					</textarea>
				</div>
				<button class="outlined-btn" type="button" @click="updateFaq">
					등록
				</button>
				<LoadingSpinner :loading="isLoading" />
			</div>
		</div>
	</div>
</template>

<script>
import SelectBoxAdmin from '@/components/common/SelectBoxV2';
import { updateFaq, fetchFaqDetail } from '@/api/faq';
import LoadingSpinner from '@/components/common/LoadingSpinner';
export default {
	components: { LoadingSpinner, SelectBoxAdmin },
	data() {
		return {
			options: [
				{ id: 'AC', label: '계정' },
				{ id: 'PY', label: '결제' },
				{ id: 'SH', label: '배송' },
				{ id: 'PD', label: '프로그램 및 굿즈' },
				{ id: 'HP', label: '홈페이지 이용' },
				{ id: 'ETC', label: '기타' },
			],
			selected: {},
			title: '',
			contents: '',
			id: '',
			isLoading: false,
		};
	},
	created() {
		this.id = this.$route.params.id;
		this.fetchFaqDetail(this.id);
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		updateSelected: function(message) {
			this.selected = message;
		},
		async fetchFaqDetail(id) {
			let result;
			this.isLoading = true;
			try {
				result = await fetchFaqDetail(id);
			} catch (e) {
				console.log(e);
			} finally {
				let selected = this.options.filter(
					item => item.id == result.data.categoryId,
				);
				this.selected = selected[0];
				this.title = result.data.title;
				this.contents = result.data.contents;
				this.isLoading = false;
			}
		},
		async updateFaq() {
			if (this.isEmptyObject(this.selected)) {
				this.errMsg = '카테고리를 선택해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '내용을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let data = {
				categoryId: this.selected.id,
				title: this.title,
				contents: this.contents,
			};
			this.isLoading = true;
			try {
				let result = await updateFaq(this.id, data);
				if (result.status == 200) {
					alert('자주 묻는 질문이 수정되었습니다');
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		isEmptyObject(param) {
			return Object.keys(param).length === 0 && param.constructor === Object;
		},
	},
};
</script>
